<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-alert title="" type="info" center show-icon :closable="false"> </el-alert>
      <!--  -->
      <el-steps :space="200" :active="activeIndex - 0" finish-status="success" align-center>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>

      <!-- tab -->
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px" label-position="top">
        <el-tabs v-model="activeIndex" :tab-position="'left'" :before-leave="beforeTabLeave" @tab-click="tabClicked">
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="editForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input v-model="editForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model="editForm.goods_weight" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number" type="number">
              <el-input v-model="editForm.goods_number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_cat">
              <el-cascader v-model="editForm.goods_cat" :options="catelist" :props="cateProps" props.expandTrigger="hover" @change="handleChange"></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <!-- Item -->
            <el-form-item :label="item.attr_name" v-for="item in manyTableData" :key="item.attr_id">
              <!--  -->
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox :label="cb" v-for="(cb, i) in item.attr_vals" :key="i" border></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item :label="item.attr_name" v-for="item in onlyTableData" :key="item.attr_id">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <!-- action API -->
            <el-upload :action="uploadURL" :on-preview="handlePreview" :file-list="imgList" :on-remove="handleRemove" list-type="picture" :headers="headerObj" :on-success="handleSuccess">
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <!--  -->
            <quill-editor v-model="editForm.goods_introduce"></quill-editor>
            <!--  -->
            <el-button type="primary" class="btnEdit" @click="edit"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="previewVisible" width="50%">
      <img :src="previewPath" alt="" class="previewImg" />
    </el-dialog>
  </div>
</template>

<script>
import _ from 'lodash'
export default {
  data() {
    return {
      activeIndex: '0',
      // 
      editForm: {
        goods_name: '',
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        // 
        goods_cat: [],
        // 
        pics: [],
        // 
        goods_introduce: '',
        attrs: []
      },
      editFormRules: {
        goods_name: [{ required: true, message: '', trigger: 'blur' }],
        goods_price: [{ required: true, message: '', trigger: 'blur' }],
        goods_weight: [{ required: true, message: '', trigger: 'blur' }],
        goods_number: [{ required: true, message: '', trigger: 'blur' }],
        goods_cat: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      catelist: [],
      cateProps: {
        label: 'cat_name',
        value: 'cat_id',
        children: 'children'
      },
      // 
      manyTableData: [],
      // 
      onlyTableData: [],
      // 
      imgList: [],
      // 
      tmpImgList: [],
      // URL 
      uploadURL: 'https://api.naccl.top/vue/shop/api/private/v1/upload',
      // headers
      headerObj: {
        Authorization: window.sessionStorage.getItem('token')
      },
      previewPath: '',
      previewVisible: false
    }
  },
  created() {
    this.getCateList()
    this.getGoodsInfo()
  },
  methods: {
    // 
    async getGoodsInfo() {
      const { data: res } = await this.$http.get(`goods/${this.$route.params.id}`)

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.editForm = res.data
      console.log(this.editForm);
      // split map  ['1','3','6'] => [1,3,6]
      this.editForm.goods_cat = this.editForm.goods_cat.split(',').map(Number)
      this.getAttrData()
      this.getImgList()
    },
    // 
    async getCateList() {
      const { data: res } = await this.$http.get('categories')

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.catelist = res.data
      // console.log(this.catelist)
    },
    // 
    getAttrData() {
      this.editForm.attrs.forEach(item => {
        if (item.attr_sel === 'many') {
          // 
          item.attr_vals = item.attr_vals ? item.attr_vals.split(',') : []
          // 
          item.attr_value = item.attr_value ? item.attr_value.split(',') : []
          this.manyTableData.push(item)
        } else if (item.attr_sel === 'only') {
          this.onlyTableData.push(item)
        }
      })
    },
    // 
    getImgList() {
      this.editForm.pics.forEach(item => {
        this.imgList.push({
          name: item.pics_id,
          url: item.pics_big_url,
          pic: item.pics_big
        })
      })
    },
    // 
    handleChange() {
      // console.log(this.editForm.goods_cat)
      if (this.editForm.goods_cat.length !== 3) {
        this.editForm.goods_cat = []
      }
    },
    beforeTabLeave(activeName, oldActiveName) {
      // console.log(':' + oldActiveName)
      // console.log(':' + activeName)
      // return false
      if (oldActiveName === '0' && this.editForm.goods_cat.length !== 3) {
        this.$message.error('!')
        return false
      }
    },
    async tabClicked() {
      // console.log(this.activeIndex)
      // 
      if (this.activeIndex === '1') {
        const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, {
          params: { sel: 'many' }
        })

        if (res.meta.status !== 200) {
          return this.$message.error('!')
        }

        // console.log(res.data)
        res.data.forEach(item => {
          item.attr_vals = item.attr_vals.length === 0 ? [] : item.attr_vals.split(' ')
        })
        this.manyTableData = res.data
      } else if (this.activeIndex === '2') {
        const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, {
          params: { sel: 'only' }
        })

        if (res.meta.status !== 200) {
          return this.$message.error('!')
        }

        console.log(res.data)
        this.onlyTableData = res.data
      }
    },
    // 
    handlePreview(file) {
      // console.log(file)
      this.previewPath = file.response.data.url
      this.previewVisible = true
    },
    // 
    handleRemove(file) {
      // console.log(file)
      if (file.response) {
        // 1. 
        const filePath = file.response.data.tmp_path
        // 2.  pics 
        const i = this.tmpImgList.findIndex(x => x.pic === filePath)
        // 3.  splice   pics 
        this.tmpImgList.splice(i, 1)
      } else {
        const filePath = file.pic
        const i = this.imgList.findIndex(x => x.pic === filePath)
        this.imgList.splice(i, 1)
      }
    },
    // 
    handleSuccess(response) {
      // console.log(response)
      // 1. 
      const picInfo = { pic: response.data.tmp_path }
      // 2. , push  pics 
      this.editForm.pics.push(picInfo)
      // console.log(this.editForm)
    },
    // 
    edit() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) {
          return this.$message.error('!')
        }
        // 
        const pics = []
        this.imgList.forEach(item => {
          pics.push({ pic: item.pic })
        })
        pics.push(...this.tmpImgList)
        const form = {
          goods_name: _.cloneDeep(this.editForm.goods_name),
          goods_price: this.editForm.goods_price,
          goods_number: this.editForm.goods_number,
          goods_weight: this.editForm.goods_weight,
          goods_introduce: _.cloneDeep(this.editForm.goods_introduce),
          pics: _.cloneDeep(pics),
          attrs: _.cloneDeep(this.editForm.attrs)
        }
        // 
        this.manyTableData.forEach(item => {
          const newInfo = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals.join(' ')
          }
          this.editForm.attrs.push(newInfo)
        })
        // 
        this.onlyTableData.forEach(item => {
          const newInfo = { attr_id: item.attr_id, attr_value: item.attr_vals }
          this.editForm.attrs.push(newInfo)
        })
        form.attrs = this.editForm.attrs
        // console.log(form)

        // 
        // 
        // 
        const { data: res } = await this.$http.put(`goods/${this.editForm.goods_id}`, form)
        // console.log(res)
        if (res.meta.status !== 200) {
          return this.$message.error('!')
        }

        this.$message.success('!')
        this.$router.push('/goods')
      })
    }
  },
  computed: {
    cateId() {
      if (this.editForm.goods_cat.length === 3) {
        return this.editForm.goods_cat[2]
      }
      return null
    }
  }
}
</script>

<style lang="less" scoped>
.el-checkbox {
  margin: 0 10px 0 0 !important;
}

.previewImg {
  width: 100%;
}

.btnEdit {
  margin-top: 15px;
}
</style>
